*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.header{
    background-image: linear-gradient(rgba(0,0,0,0.8), rgba(0,0,0,0.8)), url(https://images.unsplash.com/photo-1519336305162-4b6ed6b6fc83?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80);
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100vh;
}

#search{
    width: 350px;
}

h1{
    color: #ffffff;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    margin-top: auto;
}